import MescrollUni from "@/components/mescroll-uni/mescroll-uni.vue"
import uniCard from "@/components/uni-card/uni-card"
import uniTag from '@/components/uni-tag/uni-tag.vue'
import uniIcons from '@/components/uni-icons/uni-icons.vue'
import auditIdea from '@/pages/index/audit-idea.vue'
import {
	mapState,
	mapGetters
} from 'vuex'
import {
	getRandomArrayElements,
	filePreview,
	formatAuditStatus,
	formatProjectType
} from '@/common/utils/index.js'
import {
	formatDate,
	formatDateDay
} from '@/common/utils/datetime.js'
export default {
	components: {
		MescrollUni,
		uniCard,
		uniTag,
		uniIcons,
		auditIdea
	},
	computed: {
		...mapState(['hasLogin']),
		...mapGetters(['user'])
	},
	data() {
		return {
			mescroll: null,
			isPass: false,
			selectedProject: {},
			// 下拉刷新的常用配置
			downOption: {
				use: true, // 是否启用下拉刷新; 默认true
				auto: false, // 是否在初始化完毕之后自动执行下拉刷新的回调; 默认true
			},
			// 上拉加载的常用配置
			upOption: {
				use: false, // 是否启用上拉加载; 默认true
				auto: true, // 是否在初始化完毕之后自动执行上拉加载的回调; 默认true
				page: {
					num: 0, // 当前页码,默认0,回调之前会加1,即callback(page)会从1开始
					size: 10 // 每页数据的数量,默认10
				},
				noMoreSize: 5, // 配置列表的总数量要大于等于5条才显示'-- END --'的提示
				empty: {
					tip: '暂无相关数据'
				},
				toTop: {
					src: '/static/img/backtop.png'
				},
				textNoMore: '没有更多数据了'
			},
			// 列表数据
			cardList: [],
			// 工单类型
			categoryList: [],
			categoryDic: {},
			// 项目列表
			projectList: [],
			projectDic: {}
		}
	},
	mounted() {
		if (this.user && this.hasLogin) {
			this.initData()
		}
	},
	methods: {
		formatDate,
		formatDateDay,
		filePreview,
		formatAuditStatus,
		formatProjectType,
		// mescroll组件初始化的回调,可获取到mescroll对象
		mescrollInit(mescroll) {
			// 如果this.mescroll对象没有使用到,则mescrollInit可以不用配置
			this.mescroll = mescroll
		},
		/*下拉刷新的回调, 有三种处理方式: */
		downCallback(mescroll) {
			// 第2种: 下拉刷新和上拉加载调同样的接口, 那以上请求可删, 直接用mescroll.resetUpScroll()代替
			mescroll.resetUpScroll() // 重置列表为第一页 (自动执行 page.num=1, 再触发upCallback方法 )
		},
		/*上拉加载的回调*/
		upCallback(mescroll) {
			if (!this.user) {
				mescroll.endErr()
				return
			}

			/**
			 * todo: api请求分页查询项目待办
			 */
			setTimeout(() => {
				this.cardList = getRandomArrayElements(this.cardList, 2)
				// 成功隐藏下拉加载状态
				// 方法一(推荐): 后台接口有返回列表的总页数 totalPage
				mescroll.endByPage(this.cardList.length, 3)
				this.$nextTick(() => {
					mescroll.endSuccess(this.cardList.length)
				})
			}, 2000)

			// // 此时mescroll会携带page的参数:
			// let pageNum = mescroll.num // 页码, 默认从1开始
			// let pageSize = mescroll.size // 页长, 默认每页10条

			// this.$minApi.pageQueryProjectTodo({}, pageNum, pageSize).then(res => {
			// 	// 接口返回的当前页数据列表 (数组)
			// 	let curPageData = res.rows
			// 	// 接口返回的总页数 (比如列表有26个数据,每页10条,共3页; 则totalPage值为3)
			// 	let totalPage = res.totalPageCount
			// 	// 接口返回的总数据量(比如列表有26个数据,每页10条,共3页; 则totalSize值为26)
			// 	let totalSize = res.total
			// 	// 接口返回的是否有下一页 (true/false)
			// 	let hasNext = res.hasNextPage

			// 	if (mescroll.num == 1) this.cardList = [] //如果是第一页需手动置空列表
			// 	this.cardList = this.cardList.concat(curPageData) //追加新数据

			// 	// 成功隐藏下拉加载状态
			// 	// 方法一(推荐): 后台接口有返回列表的总页数 totalPage
			// 	mescroll.endByPage(curPageData.length, totalPage)
			// 	this.$nextTick(() => {
			// 		mescroll.endSuccess(curPageData.length)
			// 	})
			// }).catch(() => {
			// 	// 失败隐藏下拉加载状态
			// 	mescroll.endErr()
			// })
		},
		clickCard(item) {
			uni.navigateTo({
				url: '/pages/index/project/detail-project?data=' + JSON.stringify(item)
			})
		},
		adjustClick(item) {
			uni.navigateTo({
				url: '/pages/index/project/adjust-project?data=' + JSON.stringify(item)
			})
		},
		passClick(item) {
			this.selectedProject = item
			this.isPass = true
			this.$refs.popupAuditIdeaRef.$refs.share.open()
		},
		unPassClick(item) {
			this.selectedProject = item
			this.isPass = false
			this.$refs.popupAuditIdeaRef.$refs.share.open()
		},
		initData() {
			uni.showLoading({
				title: '正在初始化数据...'
			})
			this.getTicketCategorys().then(res => {
				let _categoryDic = {};
				res.forEach(function(item) {
					_categoryDic[item.workflowId] = item
				})
				this.categoryList = res;
				this.categoryDic = _categoryDic;
				return this.findProjectsByCondition()
			}).then(res => {
				let _projectDic = {}
				res.forEach(function(item) {
					_projectDic[item.id] = item
				})
				this.projectList = res;
				this.projectDic = _projectDic;

				this.getTicketTasksByStatus('10')
			})
		},
		getTicketCategorys() {
			//获得工单定义
			return new Promise((resolve, reject) => {
				this.$api.getTicketCategorys([]).then(res => {
					resolve(res.data)
				})
			})
		},
		findProjectsByCondition(params) {
			//获得项目
			return new Promise((resolve, reject) => {
				this.$api.findProjectsByCondition(params).then(res => {
					resolve(res.data)
				})
			})
		},
		getTicketTasksByStatus(params) {
			uni.showLoading({
				title: '正在查询数据...'
			})
			//获得我的任务
			this.$api.getTicketTasksByStatus(params).then(res => {
				let _cardlist = [];
				res.data.forEach(item => {
					let domainAry = item.domainPath.split('/')
					let project = this.projectDic[domainAry[domainAry.length-2]]
					let category = this.categoryDic[item.processDefinitionId]
					let card = {
						id: item.id,
						ticketTitle: item.ticketTitle,
						taskConfigName: item.taskConfigName,
						ticketNo: item.ticketNo,
						desc: item.variables?item.variables.userResponse:'无',
						addr: project?project.values.standardAddress:'无',
						senderName: item.senderName,
						auditStatus: item.taskStatus,
						createTime: this.formatDate(item.sendTime),
						attachment: []
					}
					_cardlist.push(card)
				})
				this.cardList = _cardlist
				uni.hideLoading()
			})
		},
		queryByName(name) {
			uni.showLoading({
				title: '正在查询数据...'
			})
			//获得
			this.$api.getTicketTasksByStatus([]).then(res => {
				this.cardList
				uni.hideLoading()
			})
		},
		updateQuery() {
			// 无效
			// if (this.mescroll !== null) {
			// 	this.downCallback(this.mescroll)
			// }
			this.queryByName('')
		}
	}
}
